.button {
  display: inline-block;
  border: 0;
  background-color: transparent;
  transition: opacity $ease-main;
  cursor: pointer;
  color: $color-brend;
  appearance: none;
  border-radius: 5px;
  padding: em(10 15, $fz_base);
  border: 2px solid;
  text-transform: uppercase;
  transition: color $ease-main;

  &__label {
    $fz: 18;
    font-size: em($fz, $fz_base);
  }

  $b: &;

  &[disabled] {
    opacity: 0.5;
    cursor: not-allowed;
  }

  &:hover {
    color: lighten($color-brend, 5%);
  }
}
