.container {
  width: em($container-width, $fz);
  padding-left: em($container-padding, $fz);
  padding-right: em($container-padding, $fz);
  margin-left: auto;
  margin-right: auto;
  max-width: 100%;
  box-sizing: border-box;

  @include screen(lg,max) {
    $container-padding: 50;
    padding-left: em($container-padding, $fz);
    padding-right: em($container-padding, $fz);
  }

  &--flex {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &--center {
    justify-content: center;
  }

  &--relative {
    position: relative
  }
}
