.info {
  padding-top: em(90, $fz_base);
}

.info-card {
  display: flex;
  box-shadow: em(0 0 20 5, $fz_base) rgba(#aeaeae, .4);

  @include screen(lg,max) {
    flex-wrap: wrap;
  }

  &__item {    
    &--practical {
      width: 20%;
    }

    &--descr {
      flex-grow: 1;
    }

    &--clock {
      width: 25%;
      background: linear-gradient(180deg, #f7f8f9, #eff1f2);
    }

    @include screen(lg,max) {
      &--practical {
        width: 30%;
      }

      &--descr {
        width: 70%;
      }
  
      &--clock {
        width: 100%;
        min-height: em(200, $fz_base);
      }
    }

    @include screen(xs,max) {
      width: 100%;
      min-height: em(200, $fz_base);

      &:not(:first-child) {
        border-top: 1px solid rgba(#aeaeae, .2);
      }
    }
  }
}

.info-card-list {
  $self: &;

  display: flex;
  flex-direction: column;
  height: 100%;

  &:not(.info-card-list--practical) {
    padding: em(25, $fz_base);
  }  

  &--practical {
    border-right: 1px solid rgba(#aeaeae, .2);

    #{$self}__item {
      padding: em(25, $fz_base);

      @include screen(xs, min) {
        &:not(:first-child) {
          border-top: 1px solid rgba(#aeaeae, .2);
        }
      }
    }
  }

  &--descr {
    #{$self}__item {
      &:not(:first-child) {
        margin-top: em(5, $fz_base);
      }
    }
  }
  

  &__item {
    color: #3f5367;

    &--bottom {
      margin-top: auto;
    }
  }

  &__title {
    text-transform: uppercase;
    color: $color-brend;
    margin-bottom: em(10, $fz_base);

    #{$self}__label {
      $fz: 12;
      font-size: em($fz, $fz_base);
    }
  }

  &__text {
    $fz: 14;
    font-size: em($fz, $fz_base);
    text-transform: uppercase;
    color: #aeaeae;
  }

  &__num {
    $fz: 30;
    font-size: em($fz, $fz_base);
    font-weight: 600;
    color: lighten(#000, 30%);
  }
}