.intro {
  position: relative;
  background-color: hsl(278, 82%, 18%);
  padding-top: em(130, $fz_base);
  padding-bottom: em(85, $fz);

  &::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: hsla(278, 82%, 20%, 0.7);
    z-index: 3;
  }

  &__video {
    position: relative;
    max-width: em(800, $fz);
    transform: scaleX(-1);
    margin: 0 auto;
    border-radius: em(300, $fz_base);

    &::before {
      content: '';
      @include fill-img;
      z-index: 2;
      border-radius: em(300, $fz_base);
      box-shadow: inset 0 0 200px 20px hsla(278, 82%, 18%, 0.5);
    }
  }

  &__content {
    display: flex;
    flex-direction: column;

    @include fill-img;
    z-index: 3;
  }

  &__header {
    padding: em(65 70 0, $fz);
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }

  &__link {
    $fz: 24;
    font-size: em($fz, $fz_base);
    color: $color-brend;
    font-weight: 700;
  }

  &__adress {
    color: $color-light;
  }

  &__promo {
    margin-top: auto;

    @include screen(lg,max) {
      display: none;
    }
  }
}

.intro-action {
  position: relative;
  max-width: em(355, $fz);
  margin-left: auto;
  color: $color-light;

  &__text {
    $fz: 24;
    font-size: em($fz, $fz_base);
    color: hsl(90, 69%, 72%)
  }

  &__link {
    position: absolute;
    right: 0;
    bottom: em(-20, $fz);
    display: inline-block;
    padding: em(5 10, $fz);
    background-color: hsl(353, 98%, 41%);
    border-radius: 3px;
  }
}

.intro-title {
  display: flex;
  text-transform: uppercase;

  &__num {
    color: hsl(353, 98%, 41%);
    $fz: 150;
    font-size: em($fz, $fz_base);
    line-height: 0.75;
    margin-bottom: em(-5, $fz);
  }

  &__descr {
    display: flex;
    flex-direction: column;
    height: auto;
    justify-content: space-between;
    color: $color-light;
    line-height: 1.15;
  }

  &__thin {
    $fz: 14;
    font-size: em($fz, $fz_base);
    text-transform: lowercase;
  }

  &__main {
    $fz: 26;
    font-size: em($fz, $fz_base);
  }

  &__subtitle {
    $fz: 12;
    font-size: em($fz, $fz_base);
  }
}

.logo {
  position: relative;
  display: inline-block;
  width: em(205, $fz);
  height: em(50, $fz);

  &__picture,
  img {
    @include fill-img;
  }
}

.video {
  position: relative;
  overflow: hidden;
  @include ratio(1920, 1080);

  &__item {
    @include fill-img;
  }
}
