.navigation {
  color: $color-light;
}

.navigation-list {
  display: flex;
  text-transform: uppercase;
  font-weight: 500;

  @include screen(lg,max) {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    width: 70%;
    height: 100%;
    flex-direction: column;
    background-color: $color-brend;
    padding: em(80 20 50 120, $fz_base);
    z-index: 1;
    transform: translateX(-120%);
    transition: transform $ease-main;

    &.is-active {
      transform: translateX(0);
    }
  }

  @include screen(sm,max) {
    width: 100%;
  }

  &__item {
    @include screen(lg, max) {
      &:not(:first-child) {
        margin-top: em(40, $fz_base);
      }
    }

    @include screen(lg, min) {
      &:not(:first-child) {
        margin-left: em(70, $fz_base);
      }
    }
  }

  &__link {
    $fz: 24;
    font-size: em($fz, $fz_base);

    @include screen(lg,min) {
      $fz: 18;
      font-size: em($fz, $fz_base);
      transition: color $ease-main;

      &:hover {
        color: lighten($color-brend, 10%);
      }
    }
  }
}

.burger {
  position: relative;
  width: em(50, $fz_base);
  height: em(35, $fz_base);
  z-index: 2;

  $self: &;

  @include screen(lg,min) {
    display: none;
  }

  &__item {
    position: absolute;
    width: 100%;
    left: 0;
    height: em(5, $fz_base);
    background-color: $color-light;
    transition: transform $ease-main;

    &--top {
      top: 0;
      #{$self}.is-active & {
        top: 50%;
        transform: translateY(-50%) rotate(45deg);
      }
    }

    &--middle {
      top: 50%;
      transform: translateY(-50%);

      #{$self}.is-active & {
        display: none;
      }
    }

    &--bottom {
      bottom: 0;

      #{$self}.is-active & {
        bottom: 50%;
        transform: translateY(50%) rotate(-45deg);
      }
    }
  }
}
