.calculator {
  &__form {
    margin-bottom: em(20, $fz_base);
  }

  &__descr {
    $fz: 14;
    font-size: em($fz, $fz_base);
    color: #3a4f63;
  }
}

.calculator-form {
  display: flex;
  align-items: center;
  box-shadow: em(0 0 30 5, $fz_base) rgba(#5555551f, .2);
  height: em(100, $fz_base);
  background-color: #fff;
  
  @include screen(lg,max) {
    flex-wrap: wrap;
    height: auto;
  }

  &__field {
    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 100%;
    
    border: 0.5px solid #5555551f;     
  }

  .calculator-form-field {
    position: relative;
    
    &--btn {
      flex-grow: 1;
    }

    @include screen(lg,max) {
      &--select {
        width: 50%;
      }

      &--count {
        width: calc(100 / 3 * 1%);
      }
    }

    @include screen(xs,max) {
      width: 100%;
    }
  } 
}

.calculator-form-field {
  $self: &;

  padding: em(25, $fz_base);

  &--btn {
    background: linear-gradient(180deg, #0081ff, #00b5ff);
    padding: 0;
    border: 0;

    #{$self}__label {
      $fz: 24;
      font-size: em($fz, $fz_base);
      min-height: em(100, $fz);
      color: $color-light;
      text-transform: uppercase;
      cursor: pointer;
    }
  }

  &--select {
    cursor: pointer;

    &:hover {
      #{$self}__dropdown {
        display: block;
      }
    }
  }

  &__dropdown {
    display: none;
    position: absolute;
    top: calc(100% - #{em(15, $fz_base)});
    left: em(20, $fz_base);
    z-index: 2;
  }

  &__title {
    text-transform: uppercase;
    color: $color-brend;
    margin-bottom: em(5, $fz_base);

    #{$self}__label {
      $fz: 13;
      font-size: em($fz, $fz_base);
    }
  }

  &__arrow {
    display: inline-block;
    margin-left: em(20, $fz_base);
    width: em(25, $fz_base);
    height: em(25, $fz_base);
  }

  &__input {
    color: #89919a;
    text-transform: uppercase;

    #{$self}__label {
      $fz: 24;
      font-size: em($fz, $fz_base);
    }
  }

  &__select {
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
  }

  &__count {
    #{$self}__label {
      $fz: 14;
      font-size: em($fz, $fz_base);

      &--num {
        $fz: 28;
        font-size: em($fz, $fz_base);
        color: #354a5f;
      }
    }
  }
}

.calculator-form-dropdown {
  width: 100%;
  background-color: #fff;
  box-shadow: em(5 5 30 5, $fz_base) rgba(#5555551f, .2);
  border-radius: em(5, $fz_base);
  overflow-x: hidden;
  max-height: em(291, $fz_base);

  &__item {    
    padding: em(10 0, $fz_base);
    cursor: pointer;
    padding: em(25, $fz_base);
    transition: color $ease-main, background $ease-main; 
    text-transform: uppercase;  
    color: #89919a;

    &:hover {
      color: $color-light;
      background: linear-gradient(180deg, #0081ff, #00b5ff);
    }
  }

  &__label {
    $fz: 17;
    font-size: em($fz, $fz_base);    
  }
}