.category {
  position: relative;
  padding-top: em(20, $fz);
  padding-bottom: em(50, $fz);

  &__letter {
    margin-left: em(-100, $fz);
  }
}

.category-decor {
  position: relative;
  display: block;

  &__item {
    position: absolute;
    display: flex !important;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
  }

  &__circle {
    position: relative;
    display: inline-block;
    background: #fff;
    border-radius: 100%;

    &::before {
      content: '';
      position: absolute;
      top: -3px;
      right: -3px;
      bottom: -3px;
      left: -3px;
      z-index: -1;
      border-radius: 100%;
      background: linear-gradient(180deg,#e9c1fb 0%, #a39efe 40%, #bfeffb 100%);
      animation: anim-gradient 3s linear infinite;
    }

    &--xl {
      width: em(500, $fz);
      height: em(500, $fz);
    }

    &--lg {
      width: em(400, $fz);
      height: em(400, $fz);
    }

    &--md {
      width: em(300, $fz);
      height: em(300, $fz);
    }

    &--sm {
      width: em(200, $fz);
      height: em(200, $fz);
    }
  }
}

.category-letter {
  position: relative;
  z-index: 2;
  width: em(240, $fz);
  height: em(240, $fz);
  border-radius: 5px;
  background: linear-gradient(180deg, hsl(36, 99%, 54%) 0%, hsl(36, 99%, 54%) 20%, hsl(0, 0%, 100%) 97%);
}

.category-letter-slider {
  overflow: hidden;
  width: 100%;
  height: 100%;

  &__slide {
    width: 100%;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: center;

    color: $color-light;
    cursor: default;
  }

  &__label {
    $fz: 124;
    font-size: em($fz, $fz_base);
  }
}

.category-car-slider {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 3;
  overflow: hidden;

  &__slide {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.category-car {
  $self: &;

  &__picture {
    position: relative;
    overflow: hidden;
    @include ratio(300, 170);
  }

  &__image {
    @include fill-img;
  }

  &--a {
    width: em(400, $fz);

    #{$self}__picture {
      @include ratio(2250, 1194);
    }
  }

  &--b {
    width: em(400, $fz);
    margin-bottom: em(-110, $fz);

    #{$self}__picture {
      @include ratio(1511, 670);
    }
  }

  &--c {
    width: em(400, $fz);
    margin-left: em(-110, $fz);

    #{$self}__picture {
      @include ratio(1402, 890);
    }
  }

  &--d {
    width: em(600, $fz);
    margin-bottom: em(-50, $fz);
    margin-left: em(-210, $fz);

    #{$self}__picture {
      @include ratio(1689, 717);
    }
  }
}

.slider-btn-wrap {
  position: relative;
  display: flex;
  z-index: 3;
}

.slider-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(180deg, hsl(209, 100%, 50%) 0%, hsl(198, 100%, 50%) 100%);
  position: static;
  color: $color-light;
  width: em(50, $fz);
  height: em(50, $fz);

  &:not(:first-child) {
    margin-left: em(10, $fz);
  }

  &::after {
    $fz: 25;
    font-size: em($fz, $fz_base);
  }
}
