.tabs {
  $self: &;
  $tab-gap: 25;

  display: flex;  
  justify-content: space-between;
  

  @include screen(xs,max) {
    overflow-x: scroll;
    flex-wrap: nowrap;
    -ms-overflow-style: none;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      width: 0;
      height: 0;
      background: transparent;
    }
  }

  &__tab {    
    flex: 1;
    color: #354a5f;
    display: inline-block;
    border-bottom: 2px solid #e1e1e1;
    transition: border-color $ease-main, opacity $ease-main;
    padding-bottom: em(10, $fz_base);
    cursor: pointer;
    text-transform: uppercase;
    text-align: center;
    opacity: 0.5;

    @include screen(xs,max) {
      flex: auto;
      width: 70%;
      flex-shrink: 0;
    }

    #{$self}__label {
      $fz: 14;
      font-size: em($fz, $fz_base);
    }

    &.is-active {
      border-color: #ff0000;
      opacity: 1;
    }
  }

  & + .tabs-content {
    margin-top: em(30, $fz_base);
  }
}

.tabs-content {
  &__wrapper {
    display: none;
    opacity: 0;
    transition: opacity $ease-main, transform $ease-main;
    transform: translate3d(0, 10px, 0);

    &.is-active {
      display: block;
      opacity: 1;
      transform: translate3d(0, 0, 0);
    }
  }

  &__bg {
    position: relative;
    height: em(400, $fz_base);
    overflow: hidden;
    border-radius: em(10, $fz_base);
    box-shadow: em(0 0 20 5, $fz_base) rgba(#aeaeae, .4);

    + div {
      margin-top: em(25, $fz_base);
    }
  }

  &__picture {
    @include fill-img;
    
    @include ratio(2048, 1367);
  }

  &__image {
    @include fill-img;
  }
}
