.contacts {
  margin-top: em(150, $fz_base);
  margin-bottom: em(150, $fz_base);
}

.contacts-card {
  $self: &;
  
  display: flex;
  border-radius: em(10, $fz_base);
  box-shadow: em(0 0 20 5, $fz_base) rgba(#aeaeae, .4);
  overflow: hidden;

  @include screen(lg,max) {
    flex-direction: column;
  }

  &__address {
    padding: em(40 35, $fz_base);
    flex-grow: 1;
  }

  &__map {
    width: 40%;
    position: relative;
    overflow: hidden;
    
    @include screen(lg,max) {
      width: 100%;
      height: em(500, $fz_base);
    }
  }

  &__email {
    color: #89919a;
    display: block;
    text-align: right;
    margin-top: em(30, $fz_base);

    #{$self}__label {
      $fz: 14;
      font-size: em($fz, $fz_base);

      &--brend {
        color: #0a6ed1;
      }
    }
  }

  &__iframe {
    border: 0;
    @include fill-img;
  }
}

.contacts-card-list {
  $self: &;
  overflow-y: auto;
  max-height: 100%;

  &__item {
    display: flex;
    flex-direction: column;
    background-color: rgba(#565d64, .1);
    padding: em(20 25, $fz_base);
    border-radius: em(10, $fz_base);
    color: #89919a;
    fill: #89919a;
    
    &:not(:first-child) {
      margin-top: em(10, $fz_base);
    }
  }

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: em(30, $fz_base);
    color: #247dd6;

    #{$self}__info {
      font-weight: 600;

      #{$self}__label {
        $fz: 16;
        font-size: em($fz, $fz_base);
      }
    }
  }

  &__icon {
    width: em(15, $fz_base);
    height: em(15, $fz_base);
    margin-right: em(10, $fz_base);
  }

  &__label {
    $fz: 14;
    font-size: em($fz, $fz_base);
  }

  &__info {
    display: flex;
    align-items: center;

    &:not(:first-child) {
      margin-top: em(10, $fz_base);
    }
  }

  &__mark {
    color: $color-light;
    padding: em(5 10, $fz_base);
    border-radius: em(5, $fz_base);
    background-color: #89919a;

    &--main {
      background-color: #247dd6;
    }

    #{$self}__label {
      $fz: 12;
      font-size: em($fz, $fz_base);
    }
  }
}

.contacts-form {
  $self: &;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: #272f4a;
  height: 100%;
  overflow: hidden;

  &__title {
    #{$self}__label {
      $fz: 24;
      font-size: em($fz, $fz_base);
    }
  }

  &__item {
    position: relative;
    margin-top: em(20, $fz_base);
    color: #89919a;
    border-radius: em(3, $fz_base);
    height: em($height-input, $fz_base);  
    border: 1px solid #89919a;
    border-top: 0;
  }

  &__placeholder {
    position: absolute;
    top: 0;
    left: em($padding-left-input, $fz_base);
    transform: translateY(-50%);    

    &::before,
    &::after {
      content: '';
      position: absolute;
      top: 50%;  
      width: em(1000, $fz_base);    
      height: 1px;
      transform: translateY(-50%);
      background: #89919a;
    }

    &::before {
      left: em(-10, $fz_base);
    }

    &::after {
      right: em(-10, $fz_base);
      
    }
  }

  &__input {
    display: block;
    width: 100%;
    height: 100%;
    z-index: 2;
    padding: em(0 $padding-left-input, $fz_base);
    $fz: 18;
    font-size: em($fz, $fz_base);

    &:focus {

    }
  }

  &__descr {
    @include screen(lg,max) {
      margin: em(20 0, $fz_base);
    }

    #{$self}__label {
      $fz: 14;
      font-size: em($fz, $fz_base);
    }
  }

  &__btn {
    @include screen(lg,min) {
      align-self: flex-start;
    }
  }
}